.workcard-container {
	display: flex;
	align-items: flex-start;
	gap: 40px;
	padding: 30px;
	border-radius: 32px;
	background: #ffffff;
	box-shadow: 0 8px 24px rgba(0, 0, 0, 0.1);
	margin-bottom: 40px;
	flex-wrap: wrap;
}
.thumbnail-wrapper {
	width: 400px;
	height: 250px;
	background-color: #fff; /* 또는 transparent */
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: top;
  }
  
  .workcard-thumbnail {
	max-width: 100%;
	max-height: 100%;
	object-fit: contain;
	border-radius: 12px;           /* ✅ 이미지 자체도 둥글게 */
  }
  .extra-thumbnail {
	max-width: 50%;
	max-height: 50%;
	object-fit: contain;
	border-radius: 12px;           /* ✅ 이미지 자체도 둥글게 */
  }
  
  

.workcard-info {
	flex: 1;
	min-width: 300px;
	display: flex;
	flex-direction: column;
	justify-content: space-between;
}


.workcard-header {
	display: flex;
	flex-direction: row;
	gap: 12px;
	margin-bottom: 20px;
	position: relative;
}


.workcard-left p,
.workcard-right p {
	margin: 8px 0;
	color: #333;
}
.workcard-left {
	width: 100%;
}
.workcard-right {
	position: absolute;
	top: 0;
	right: 0;
	display: flex;
	align-items: center;
	gap: 8px;
	font-weight: bold;
  }

.workcard-buttons {
	margin-top: auto;
	display: flex;
	justify-content: flex-end;
	flex-wrap: wrap;
	gap: 32px;
}
.button-group {
	display: flex;
	flex-direction: column; /* 라벨 + 버튼 수직 정렬 */
	align-items: center;    /* 버튼 중앙 정렬 */
	gap: 8px;
}

.button-label {
	font-size: 0.85rem;
	font-weight: 500;
	color: #666;
	margin-bottom: 4px;
}



.workcard-info {
	flex: 1;
	min-width: 300px;
	display: flex;
	flex-direction: column;
	justify-content: space-between;
}

.btn {
	display: flex;
	align-items: center;
	gap: 6px;
	background-color: #e0e0e0;
	color: #333;
	text-decoration: none;
	padding: 10px 20px;
	border-radius: 12px;
	font-weight: 600;
	box-shadow: 0 2px 6px rgba(0, 0, 0, 0.1);
	transition: 0.3s;
}

.btn:hover {
	opacity: 0.85;
	transform: translateY(-1px);
}

.btn-icon {
	width: 20px;
	height: 20px;
	object-fit: contain;
}



.gameTitle {
	font-size: 1.8rem;
	font-weight: 900;
	color: #222;
	margin-bottom: 12px;
	letter-spacing: -0.5px;
}

.tag {
	background: #f1f1f1;
	color: #555;
	font-size: 0.85rem;
	padding: 4px 10px;
	border-radius: 20px;
	margin-right: 6px;
	display: inline-block;
}

.date {
	font-size: 0.95rem;
	color: #888;
	font-weight: 500;
	margin-top: 8px;
	letter-spacing: 0.5px;
}
.engine-icon {
	width: 32px;
	height: 32px;
	object-fit: contain;
}