.about-section {
	padding: 60px 20px;
	background-color: #fdfdfd;
}

.about-container {
	max-width: 960px;
	margin: 0 auto;
	padding: 40px;
	background: #fff;
	border-radius: 16px;
	box-shadow: 0 8px 20px rgba(0, 0, 0, 0.06);
}

.about-title {
	font-size: 2rem;
	font-weight: bold;
	margin-bottom: 30px;
	text-align: center;
	margin: 0 auto; /* ✅ 이게 핵심! */
	color: #333;
}

.about-subtitle {
	line-height: 1.8;
	font-size: 1.05rem;
	color: #444;
	text-align: center;
	max-width: 740px;
	margin: 0 auto; /* ✅ 이게 핵심! */
	padding: 0 10px;
}



.about-subtitle p {
	margin-bottom: 20px;
}

.image-container {
	text-align: center;
	margin: 30px 0;
}

.image-container img {
	max-width: 100%;
	border-radius: 12px;
	box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
}

.image-caption {
	font-size: 0.85rem;
	color: #666;
	margin-top: 8px;
	font-style: italic;
}

/* 작은 이미지용 (세로 길이 강조) */
.small-image {
	max-width: 80%;
	max-height: 300px;
}
