.contact-section {
	padding: 80px 20px;
	background-color: #fdfdfd;
	display: flex;
	flex-direction: column;
	align-items: center;
}

.contact-container {
	max-width: 900px;
	width: 100%;
	background: #fff;
	border-radius: 16px;
	box-shadow: 0 8px 20px rgba(0, 0, 0, 0.1);
	padding: 40px;
	margin-bottom: 40px;
}

.contact-title {
	font-size: 2rem;
	font-weight: bold;
	margin-bottom: 30px;
	color: #333;
	text-align: center;
}

.contact-content {
	display: flex;
	gap: 40px;
	align-items: center;
	justify-content: space-between;
	flex-wrap: wrap;
}

.contact-text {
	flex: 1;
	font-size: 1.05rem;
	line-height: 1.7;
	color: #444;
}

.contact-image {
	flex: 1;
	text-align: center;
}

.contact-image img {
	max-width: 300px;
	width: 100%;
	border-radius: 12px;
	box-shadow: 0 4px 10px rgba(0, 0, 0, 0.15);
}

/* Socials 영역 */
.socials-container {
	text-align: center;
}

.socials-title {
	font-size: 1.4rem;
	font-weight: bold;
	margin-bottom: 16px;
	color: #444;
}

.contact-socials {
	display: flex;
	justify-content: center;
	gap: 20px;
}

/* 반응형 */
@media (max-width: 768px) {
	.contact-content {
		flex-direction: column;
		gap: 20px;
	}

	.contact-title {
		font-size: 1.6rem;
	}

	.contact-image img {
		max-width: 220px;
	}
}
